<template>
  <div class="container Project_product">
    <div id="loadingDiv" v-show="value">
      <div class="loader"></div>
    </div>

    <div v-if="cart.length === 0">
      <div class="d-flex justify-content-center text-center">
        <div class="mt-5">
          <h2 class="favorite__title mb-3 h5 mt-4 mb-4">
            <i class="fas fa-cart-plus" style="font-size: 7rem;color: unset;"></i>
            購物車內無商品，快去挑挑吧！
          </h2>
          <a
            href="#"
            @click.prevent="
              category = '所有商品';
              toProducts();
            "
            class="text-dark"
          >
            <button class="btn all_btn">
              <i class="fas fa-angle-left mr-2"></i>去逛逛
            </button></a
          >
        </div>
      </div>

      <!-- 購物車內無任何商品
      <a href="/"><i class="fas fa-angle-left mr-2"></i>現在就去逛逛</a> -->
    </div>
    <div v-else>
      <div class="bar__container">
        <ul class="bar row" id="bar">
          <li class="active">STEP 01 購物車</li>
          <li>STEP 02 填寫資料</li>
          <li>STEP 03 訂購完成</li>
        </ul>
      </div>

      <div class="row mt-5">
        <div class="col-md-12 col-sm-12">
          <table class="rwd-table">
            <tr class="tr-one">
              <th>品名</th>
              <th class="text-center count-th" style="width: 200px">數量</th>
              <th class="text-center">單價</th>
              <th class="text-center">小計</th>
              <th class="text-center">移除</th>
            </tr>
            <tr v-for="item in cart" :key="item.product.id">
              <td class="align-middle" scope="row" data-th="品名">
                <router-link
                  :to="`/product/${item.product.id}`"
                  target="_blank"
                >
                  <img
                    :src="item.product.imageUrl"
                    class="mr-3 d-md-inline-block"
                    width="100"
                    height="100"
                    style="object-fit: cover"
                  />
                </router-link>
                {{ item.product.title }}
              </td>
              <td class="align-middle" data-th="數量">
                <div class="input-group justify-content-center">
                  <div class="input-group-prepend">
                    <button
                      class="btn btn-outline-success"
                      @click="item.quantity--"
                      :disabled="item.quantity <= 1"
                    >
                      -
                    </button>
                  </div>
                  <input
                    type="number"
                    min="1"
                    class="form-control col-4 text-center"
                    style="background-color: #fff !important"
                    readonly
                    v-model.number="item.quantity"
                  />
                  <div class="input-group-append">
                    <button
                      class="btn btn-outline-success"
                      @click="item.quantity++"
                    >
                      +
                    </button>
                  </div>
                </div>
              </td>
              <td class="align-middle text-center" data-th="單價">
                $ {{ item.product.price }}
              </td>
              <td class="align-middle text-center" data-th="小計">
                $ {{ item.product.price * item.quantity }}
              </td>
              <td class="align-middle text-center" data-th="移除">
                <button
                  type="button"
                  class="btn btn-sm"
                  @click="delOneCartItem(item.product.id)"
                >
                  <i class="fas fa-times"></i>
                </button>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <hr />
      <div style="text-align: right">
        <div>
          <span class="mr-2" style="font-size: 15px"> 總金額 </span
          ><span class="mr-2" style="font-size: 15px"> NT$</span>
          <span style="font-size: 1.5rem"> {{ totalPrice }}</span>
          <span class="ml-2" style="font-size: 15px">元</span>
        </div>
        <div style="margin-top: 20px; margin-bottom: 50px">
          <router-link to="/" class="text-dark">
            <button class="btn cart-back-btn cart-btn">
              <i class="fas fa-angle-left mr-2"></i>繼續選購
            </button></router-link
          >
          <router-link to="/order" class="text-dark">
            <button class="btn btn-success cart-btn" style="margin-left: 20px">
              前往結帳
            </button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from "jquery";

export default {
  name: "MyCart",
  data() {
    return {
      cart: [],
      value: true,
    };
  },
  created() {
    this.getCart();
  },
  methods: {
    getCart() {
      const api =
        "https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping";
      this.$http.get(api).then((res) => {
        this.cart = res.data.data;
        console.log(this.cart);
        this.value = false;
      });
    },
    delOneCartItem(test) {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping/${test}`;
      this.$http.delete(api).then(() => {
        this.getCart();
        this.$bus.$emit("updateCart"); //即時更新右上角購物車icon數字
      });
    },
    toProducts() {
      this.$emit("fliterProduct", this.category);
      this.$router.push(`/products/${this.category}`);
    },
  },
  computed: {
    totalPrice() {
      return this.cart.reduce(
        (prev, i) => prev + i.product.price * i.quantity,
        0
      ); //????
    },
  },
};
</script>

<style>
@import url(https://fonts.googleapis.com/css?family=Montserrat);
* {
  box-sizing: border-box;
}

.cart-back-btn {
  border: 1px solid #218838;
  background-color: #fff;
  color: #218838;
  width: 160px !important;
  cursor: pointer;
}

.cart-btn {
  width: 160px !important;
  cursor: pointer;
}

.all_btn {
  border-radius: 22px;
  background-color: #db5669;
  font-size: 16px;
  color: #fff;
  letter-spacing: 1px;
  cursor: pointer;
}

.title {
  margin-bottom: 30px;
  text-align: center;
  color: #020304;
}

.bar__container {
  width: 100%;
  /* max-width: 600px;
  margin: 0 auto; */
}

.bar {
  counter-reset: step;
  margin: 0;
  padding: 0;
}

.bar li {
  list-style-type: none;
  /* float: inherit; */
  width: 32%;
  /* position: relative; */
  text-align: center;
  font-size: 9px;
  /* color: white; */
  /* padding-left: 5px; */
  background-color: #fff;
  border: 1px solid #dfdfdf;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  /* border-radius: 17px; */
  padding: 10px;
}

.bar li.active {
  background-color: #28a745;
  /* border-radius: 41px; */
  color: #ffffff;
  /* border-image: stretch; */
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}
@media all and (min-width: 500px) {
  .bar li {
    text-transform: uppercase;
    font-size: 17px;
  }
}
.bar li:before {
  /* content: counter(step); */
  /* counter-increment: step; */
  content: "";
}
/* .bar li:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: #ddd;
  top: 15px;
  left: -50%;
  z-index: -1;
} */
.bar li:first-child:after {
  content: none;
}
/* .bar li.active:before {
  background: #27ae60;
  border: 1px solid #89e5b0;
  color: white;
} */
.bar li.active + li.active:after {
  background: #27ae60;
}
.bar li.active:first-child + li:after:not(.active) {
  background: white;
}

/*LOADING */
.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.loader {
  margin: auto;
  font-size: 4px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid #4b5f9b;
  border-right: 1.1em solid #4b5f9b;
  border-bottom: 1.1em solid #4b5f9b;
  border-left: 1.1em solid #f8f9fc;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  transform: translateY(-50%);
  top: 50%;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

#loadingDiv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f8f9fc;
  z-index: 3;
  overflow: hidden;
}

/* .main {
  position: relative !important;
  margin-bottom: 0 !important;
} */

/* .fa-cart-plus {
  font-size: 7rem;
} */

.rwd-table {
  background: #fff;
  overflow: hidden;
  font-size: 14px;
}

.rwd-table th,
.rwd-table td {
  margin: 0.5em 1em;
}

.rwd-table {
  min-width: 100%;
}

.rwd-table .tr-one {
  background-color: #eee;
}

.rwd-table th {
  display: none;
}

/* .rwd-table td {
  display: block;
} */

.rwd-table td:before {
  content: attr(data-th) " : ";
  font-weight: bold;
  width: 4.5em;
  display: inline-block;
}

/* .rwd-table th,
.rwd-table td {
  text-align: left;
} */

.rwd-table th,
.rwd-table td:before {
  color: #000;
  font-weight: bold;
}

@media (min-width: 769px) {
  .rwd-table td:before {
    display: none;
  }
  .rwd-table th,
  .rwd-table td {
    display: table-cell;
    padding: 0.25em 0.5em;
  }
  .rwd-table th:first-child,
  .rwd-table td:first-child {
    padding-left: 0;
  }
  .rwd-table th:last-child,
  .rwd-table td:last-child {
    padding-right: 0;
  }
  .rwd-table th,
  .rwd-table td {
    padding: 10px !important;
  }
}

@media (max-width: 768px) {
  .input-group {
    width: 195px !important;
    display: inline-flex !important;
    justify-content: start !important;
  }

  .rwd-table td {
    display: block !important;
  }

  .rwd-table th,
  .rwd-table td {
    text-align: left !important;
  }

  .rwd-table tr {
    box-shadow: 1px 1px 7px -5px rgba(0,0,0,.8);
  }
}
</style>